






































import { Component, Vue } from 'vue-property-decorator';
import { Company as CompanyType } from '@/datatypes';

@Component
export default class Company extends Vue {
  get company(): CompanyType | {name: string} {
    const companyData =
      (this.$store.state.companies as CompanyType[]).find((ccompany) => ccompany.id === this.$route.params.id);
    return companyData || {name: 'loading...'};
  }
  private mounted() {
    const app = document.getElementById('app');
    if (app instanceof HTMLDivElement) {
      app.scrollTo(0, 0);
    }
  }
}
